import React, { useState } from "react";
import ClearIcon from "@mui/icons-material/Clear";
import CancelRoundedIcon from "@mui/icons-material/CancelRounded";
import MenuRoundedIcon from "@mui/icons-material/MenuRounded";
import "./Navbar.css";
import MobileNav from "./MobileNav";
import AnchorLink from "react-anchor-link-smooth-scroll";
const Navbar = () => {
  const [menu, setMenu] = useState("hero");
  const [openMenu, setOpenMenu] = useState(false);
  const toggleMenu = () => {
    setOpenMenu(!openMenu);
  };
  return (
    <>
      <MobileNav isOpen={openMenu} toggleMenu={toggleMenu} />
      <div className="navbar">
        <div className="nav-name">Portfolio</div>
        <ul className="nav-menu">
          <li>
            <AnchorLink className="anchor-link" offset={50} href="#hero">
              <p onClick={() => setMenu("hero")}>Home</p>
            </AnchorLink>
          </li>
          <li>About</li>
          <li>
            <AnchorLink className="anchor-link" offset={50} href="#skills">
              <p onClick={() => setMenu("skills")}>Skills</p>
            </AnchorLink>
          </li>
          <li>
            {" "}
            <AnchorLink className="anchor-link" offset={50} href="#project">
              <p onClick={() => setMenu("project")}>Projects</p>
            </AnchorLink>
          </li>
          <li>
            <AnchorLink className="anchor-link" offset={50} href="#contact">
              <p onClick={() => setMenu("contact")}>Contact</p>
            </AnchorLink>
          </li>
        </ul>
        <button class="menu-btn" onClick={toggleMenu}>
          <span
            class={"material-symbols-outlined"}
            style={{ fontSize: "1.2rem" }}
          >
            {openMenu ? <ClearIcon /> : <MenuRoundedIcon />}
          </span>
        </button>
        <div className="nav-connect">
          {" "}
          <AnchorLink className="anchor-link" offset={50} href="#contact">
            Connect With Me
          </AnchorLink>
        </div>
      </div>
    </>
  );
};

export default Navbar;
