import React from "react";
import "./ProjectCard.css";
const ProjectCard = ({ details }) => {
  return (
    <div className="project-card">
      <h6>{details.title}</h6>
      <div className="project-link">
        <a
          href={details.link}
          target="_blank"
          rel="noopener"
          style={{ color: "white" }}
        >
          {details.link}
        </a>
      </div>
      <ul>
        {details.description.map((item) => (
          <li key={item}>{item}</li>
        ))}
      </ul>
    </div>
  );
};

export default ProjectCard;
