import React from "react";
import "./Hero.css";
import AnchorLink from "react-anchor-link-smooth-scroll";
import profilepic2 from "../../assets/profilepic2.jpg";
import Resume from "../../assets/Resume 2.pdf";
const Hero = () => {
  return (
    <div id="hero" className="hero">
      <img src={profilepic2} alt="profile-pic" />

      <h1>
        <span>I'm Rani Singh</span>, a MERN stack developer.
      </h1>
      <p>
        I specialize in developing applications that provide exceptional user
        experiences.
      </p>
      <div className="hero-action">
        <div className="hero-connect">
          {" "}
          <AnchorLink className="anchor-link" offset={50} href="#contact">
            Connect With Me
          </AnchorLink>
        </div>
        <div className="hero-resume">
          <a href={Resume} target="_blank" className="resume">
            My Resume
          </a>
        </div>
      </div>
    </div>
  );
};

export default Hero;
