import React from "react";
import "./Project.css";
import { PROJECTS } from "../../utils/data";
import ProjectCards from "./ProjectCard/ProjectCard";
const Project = () => {
  return (
    <>
      <section id="project" className="project-container">
        <h5>Projects</h5>
        <div className="project-content">
          {PROJECTS.map((item) => (
            <ProjectCards key={item.title} details={item} />
          ))}
        </div>
      </section>
    </>
  );
};

export default Project;
